.grey-transparent-btn {
    border-radius: 14px;
    backdrop-filter: blur(14px);
    background: rgba(255, 255, 255, 0.12);
    margin-top: 38px;
    &:active {
      background-color: #29292c;
    }
    &_with-icon {
      svg {
        flex-shrink: 0;
        color: rgb(112, 117, 121);
      }
    }
  }
  
  .earn-transparent-div {
    position: relative;
    border-radius: 14px;
    backdrop-filter: blur(14px);
    background: rgba(255, 255, 255, 0.12);
    &_first {
      padding: 51px;
      margin-top: 23px;
    }
    &_second {
      padding: 142px;
      margin-top: 14px;
    }
  }
  
  .earn-transparent-btn {
    position: absolute;
    background: transparent;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    height: 90px;
    width: 100%;
    &_first {
      margin-top: 6px;
      border-radius: 4px;
    }
    &_second {
      margin-top: 6px;
      border-radius: 4px 4px 0px 0px;
    }
    &_third {
      margin-top: 96px;
    }
    &_fourth {
      margin-top: 186px;
      border-radius: 0px 0px 4px 4px;
    }
    &:active {
      background-color: #29292c;
    }
  }
  
  .earn-page {
    &__header-btn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      padding: 16px 12px;
      width: 100px;
      margin-top: 23px;
    }
    &__header-btn-content {
      display: flex;
      align-items: center;
    }
  }
  
  .IconBackground {
    width: 70px;
    height: 70px;
    background-color: var(--palette-transparent-white-05);
    border-radius: 12px;
    margin-left: 4px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
