  .earn-icon {
    margin-top: 30px;
    width: 100px;
    height: 100px;
    background-image: url("../../assets/images/coin-small.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;
  }
  
  .earn-animation {
    opacity: 0; /* Начальная прозрачность */
    animation: earn-opacity 0.25s forwards; /* Название анимации и время */
  }
  
  @keyframes earn-opacity {
    from {
      opacity: 0; /* Начальное значение прозрачности */
    }
    to {
      opacity: 1; /* Конечное значение прозрачности */
    }
  }